    border:1px solid #2a2a2a;
}


.from-container textarea{
    padding:0 1rem;
    margin-bottom:2rem;
    border-radius:0.3rem;
    border:1px solid #2a2a2a;
}

.from-container button{
    display:inline;
}


@media screen and (max-width:850px){
    .from-container{
        margin:4rem 2rem;
    }

    .from-container form{
        padding-top:2rem;
        width:90%;
    
    }
}

